@media only screen and (min-width: 2001px) {

	.wrapper-up-logo {
		background:	#555;
		height: 5%;
		padding-top: 0.1%;
		padding-bottom: 0.1%;
		text-align: left;
		img {
			margin-left: 26.1%;
		}
	}
	
	.wrapper-up {
		background: url("../media/img_hero.jpg") repeat-x;
		background-size: 71%;
		background-position: 21% 36%; 
			h1 {
				// height: 19vh;
				font-size: 3.6em;
				line-height: 0.6em;
				font-weight: bold;
				font-family: "Open Sans", sans-serif;
				text-align: left;
				margin-left: 25.8%;
				margin-top: 0;
				margin-bottom: 0;
				padding: 5.2% 0;
		}
	}
	
	.wrapper-middle {
		height: 100%;
		display: grid;
		grid-template-columns: 80% 20%;
		grid-column-gap: 18%;
		margin: 1.5% 17% 1.5% 26%;
		// padding: 1.6% 0 3.3% 0;
		padding: 2% 0;
		h1.how-it-started {
			font-size: 2.8em;
			font-weight: bold;
			line-height: 1.2em;
			padding-bottom: 2%;
			color: #ffa200;
			// margin-left: 0%; margin-top: 0;
			margin-bottom: 4%;
		};
		p.thick {
			line-height: 1.5em;
			font-size: 1em;
			margin-bottom: 0;
			text-align: center;
		}
		.feedback {
			margin: auto;
			p {
				color: #555;
				.p-header {
					font-weight: bold;
					color: #ffa200;
				}
			}
		}
		.notes {
			padding-bottom: 4.3%;
		}
	}
	
	.wrapper-middle-to-be-updated-motion {
		height: 100%;
		display: grid;
		grid-template-columns: 70% 30%;
		grid-column-gap: 18%;
		margin: 1% 12% 0 25.7%;
		// padding: 1.6% 0 3.3% 0;
		padding: 2% 0% 0% 0%;
		h1.how-it-started {
			font-size: 2.8em;
			font-weight: bold;
			color: #ffa200;
			// margin-left: 0%; margin-top: 0;
		};
		p.thick {
			line-height: 1.5em;
			font-size: 1em;
			margin-bottom: 0;
			text-align: center;
		}
		.feedback {
			margin: auto;
			p {
				color: #555;
				.p-header {
					font-weight: bold;
					color: #ffa200;
				}
			}
		}
	}
	
	.wrapper-down {
		height: 100%;
		display: grid;
		grid-template-columns: 22.5% 22.5% 22.5% 22.5%;
		/* column-gap: 3%; */
		background:	#555;
		color: #fff;
		padding: 1.0% 13% 3.5% 26%;
		border-top: 7px solid #ffc200;
		/* margin-left: auto; margin-right: auto; padding: 0% 0% 16% 28%; margin-top: 15px; */
	}
	
	.wrapper-icons {
		display: grid;
		grid-template-columns: 50% 50%;
		/* grid-column-gap: 1%; margin-left: -35%; */
	}
	
	p {
		font-size: 1em;
		text-align: left;
	}
	
	p.thin {
		text-align: left;
		line-height: 0.01em;
		font-size: 0.8em;
		.active {
			cursor: pointer;
			&:hover {
				color: #ffc200;
				text-decoration: underline;
			}
		}
	}
	
	p.thick {
		line-height: 1.4em;
		font-size: 0.9em;
	}
	
	p.thick-hidden {
		line-height: 0.39em;
		font-size: 1em;
		visibility: hidden;
	}
	
	p.thick-join-us {
		text-align: left;
		line-height: 2.0em;
		font-size: 0.8em;
	}
	
	.font-header {
		line-height: 1.2em;
		font-size: 1.1em;
		color: #ffc200;
		font-weight: bold;
		text-align: left;
		padding-top: 0.5em;
		padding-bottom: 0.7em;
	}
	
	.tooltip {
		max-width: 17.5%;
		font-weight: normal;
		line-height: 1.4em;
	}

	.button-deactivated {
		font-size: 1.35rem;
		/* padding: 5px 10px; */
		color:   #585858;
		text-align: center;
		margin: 0;
		padding: 0;
		.icon {
			cursor: pointer;
			opacity: .7;
			&:hover {
				opacity: 1;
				// filter: brightness(1.4);
			}
		}
		.icon-dislike {
			cursor: pointer;
			opacity: .7;
			transform: rotateY(-180deg);
			&:hover {
				opacity: 1;
			}
		}
		.digit {
			margin-left: 0.4vw;
			cursor: auto;
			font-size: 1em;
			font-weight: bold;
			opacity: .7;
		}
	}
	
	.button-activated {
		font-size: 1.35rem;
		/* padding: 5px 10px; */
		color: #007bff; // #1565f0
		text-align: center;
		margin: 0;
		padding: 0;
		.icon {
			cursor: pointer;
			&:hover {
				opacity: 1;
			}
		}
		.icon-dislike {
			cursor: pointer;
			transform: rotateY(-180deg);
			&:hover {
				opacity: 1;
			}
		}
		.digit {
			margin-left: 0.4vw;
			cursor: auto;
			font-size: 1em;
			font-weight: bold;
		}

		.container-for-circle {
			p.text-below-circle {
				margin: 28% 6% 1% -16%;
			};
		}
	}
	
	// img.resize-like {
	// 	width: 30px;
	// 	/* max-height: 30%; */
